body {
    background-color: rgb(55, 54, 63)
}

#List {
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: left;
    list-style: none;
    margin: 0%;
}

button {
    width: 66px;
    height: 66px;
    margin: 15px 10px 15px 15px;
    border-style: 50px solid;
    border-width: 5px;
    border-radius: 50%;
    border-color: rgb(179, 0, 0);
    font-size: 40px;
    background-color: rgb(235, 229, 182);
}